import IntroduceList from "./IntroduceList";
import { useEffect } from 'react';
import { useSelector } from 'react-redux';

function Page3(){
	let data=useSelector(pr => pr.introduce.title);
	useEffect(() => {
		let page3=document.querySelector("#page3");
		let intro=page3.firstElementChild.firstElementChild;
		intro.innerHTML=data;
	});
	
	return(
		<section id="page3">
			<div className="inner">				
				<div className="intro">
				</div>
				<div className="right">
					<IntroduceList />
				</div>			
			</div>
		</section>
	);
}

export default Page3;