import { useSelector } from 'react-redux';

function Nav(){
	let data=useSelector(pr => pr.menu);
	return(
		<nav id="gnb">
			<ul>
				{/* <li><a href="/">HOME</a></li>
				<li><a href="/">SKILLS</a></li>
				<li><a href="/">PORTFOLIO</a></li>
				<li><a href="/">ABOUT</a></li> */}
				{data.map((d,i) => <li key={i+1}><a href="/">{d}</a></li>)}
			</ul>
		</nav>	
	);
}
export default Nav;