import { Swiper, SwiperSlide } from 'swiper/react';
import { Autoplay, Pagination } from "swiper/modules";
import { useSelector } from 'react-redux';
import "swiper/css";
import "swiper/css/pagination";

function Slider(){
	let data=useSelector(pr => pr.slider);

	let sliderChange = (swiper) =>{
		let mainSwiper=document.querySelector(".mainSwiper");
		let visitBtn=mainSwiper.querySelector(".site_visit");
		let detailBtn=mainSwiper.querySelector(".view_detail");
		let slideN=swiper.realIndex;
		
		const portfolioLink=[
			{
				title:"프레스캣",
				detail: "window.open('https://zzang1537.github.io/portfolio1/project_template1.html', 'pop')",
				link: "window.open('https://zzang1537.github.io/project1/')"
			},
			{
				title:"올고다",
				detail: "window.open('https://zzang1537.github.io/portfolio1/project_template2.html', 'pop')",
				link: "window.open('https://zzang1537.github.io/project2/')"
			},
			{
				title:"오므토토마토",
				detail: "window.open('https://zzang1537.github.io/portfolio1/project_template3.html', 'pop')",
				link: "window.open('https://zzang1537.github.io/project3/')"
			},
		];

		detailBtn.setAttribute("onclick", portfolioLink[slideN].detail);
		visitBtn.setAttribute("onclick", portfolioLink[slideN].link);
	}
	return(
		<div className="visual">
			<Swiper
				spaceBetween={50}
				speed={1200}
				loop={true}			
				autoplay={{
					delay: 6000,
					disableOnInteraction: false
				}}				
				pagination={{
					el: ".swiper-pagination",
					type: "fraction"
				}}
				onSwiper={swiper => {  
					sliderChange(swiper);

					let mainSwiper=document.querySelector(".mainSwiper");
					let control=mainSwiper.lastElementChild;
					let arrow=control.lastElementChild.lastElementChild;
					let [leftBtn, rightBtn]=arrow.children;

					leftBtn.addEventListener("click", e => {
						e.preventDefault();
						swiper.slidePrev();				
					});
					rightBtn.addEventListener("click", e => {
						e.preventDefault();
						swiper.slideNext();		
					});
				}}
				onSlideChange={swiper => sliderChange(swiper) }
				modules={[Pagination, Autoplay]}
				className="swiper mainSwiper"
			>					
				<div>
					{data.map((d,i) => 
						<SwiperSlide key={i+1}>
							<img src={"./images/"+d.image} alt={d.alt} />
							<div className="keytext">
								<strong>{d.title}<span>Renewal</span></strong>
								<p>: 제작기간 <i>{d.date}</i></p>
								<ul className="des_list">
									{d.description.map((d,i) => <li key={i+1}>{d}</li> )}
								</ul>
							</div>
						</SwiperSlide>	
					)}
				</div>
				<button type="button" className="site_visit">사이트 바로가기</button>
				<button type="button" className="view_detail">디자인 가이드</button>
				<div className="intro_box">
					<div className="main_tit">Port <br />folio.</div>
					<div className="control">
						<div className="swiper-pagination"></div>
						<div className="arrow">
							<a href="/" className="left_btn"></a>
							<a href="/" className="right_btn"></a>
						</div>
					</div>							
				</div>	
			</Swiper>
			{/* <div className="swiper mainSwiper">
				<div className="swiper-wrapper">
					<div className="swiper-slide">
						<img src="/images/slider1.jpg" alt="slider1" />
						<div className="keytext">
							<strong>PRESSCAT<span>Renewal</span></strong>
							<p>: 제작기간 <i>23.6.20 ~ 7.02</i></p>
							<ul className="des_list">
								<li>#UI/UX기반 웹사이트</li>
								<li>#JavaScript Native</li>
								<li>#SASS</li>
								<li>#HTML5 + CSS3</li>
							</ul>
						</div>
					</div>
					<div className="swiper-slide">
						<img src="/images/slider2.png" alt="slider2" />
						<div className="keytext">									
							<strong>올고다<span>Renewal</span></strong>
							<p>: 제작기간 <i>23.5.15 ~ 6.05</i></p>
							<ul className="des_list">
								<li>#UI/UX기반 웹사이트</li>
								<li>#JavaScript Native</li>
								<li>#SwiperJS</li>
								<li>#HTML5 + CSS3</li>
							</ul>
						</div>
					</div>
					<div className="swiper-slide">
						<img src="/images/slider3.jpg" alt="slider3" />
						<div className="keytext">
							<strong>오므토토마토<span>Renewal</span></strong>
							<p>: 제작기간 <i>23.4.17 ~ 4.30</i></p>
							<ul className="des_list">
								<li>#UI/UX기반 웹사이트</li>
								<li>#jQuery</li>
								<li>#SwiperJS</li>
								<li>#SASS</li>
								<li>#HTML5 + CSS3</li>
							</ul>
						</div>
					</div>
				</div>
				<button type="button" className="site_visit" onclick="location.href='project_template.html'">사이트 바로가기</button>
				<button type="button" className="view_detail" onclick="location.href='project_template.html'">디자인 가이드</button>
				<div className="intro_box">
					<div className="main_tit">Port <br />folio.</div>
					<div className="control">
						<div className="swiper-pagination"></div>
						<div className="arrow">
							<a href="/" className="left_btn"></a>
							<a href="/" className="right_btn"></a>
						</div>
					</div>							
				</div>						
			</div> */}
		</div>	
	);
}
export default Slider;