import { useSelector } from 'react-redux';

function MenuUtile(){
	let data=useSelector(pr => pr.utile);
	return(
		<div className="inner_utile">
			<ul>
				{/* <li>
					<a href="https://github.com/zzang1537">									
						<i><img src="/images/w_link1.png" alt="link1" /></i>							
					</a>
				</li>
				<li>
					<a href="https://codepen.io/your-work/">									
						<i><img src="/images/w_link2.png" alt="link2" /></i>						
					</a>
				</li>
				<li>
					<a href="https://blog.naver.com/jukistyle">									
						<i><img src="/images/w_link3.png" alt="link3" /></i>						
					</a>
				</li> */}
				{ data.map((d,i) => 
					<li key={i+1}>
						<a href={d.link}>									
							<i><img src={"./images/"+d.img} alt={d.alt} /></i>							
						</a>
					</li>
				)}
			</ul>
		</div>	
	);
}
export default MenuUtile;