import { Fragment } from "react";
import { useSelector } from 'react-redux';
import { useEffect } from 'react';

function IntroduceList(){
	let firstData=useSelector(pr => pr.introduce.first);
	useEffect(()=>{
		let name=document.getElementsByClassName("name")[0];
		name.innerHTML=firstData;
	});
	let data=useSelector(pr => pr.introduce);
	let {list, last}=data;
	return(
		<Fragment>
			<div className="intro_img"><img src="./images/skill1.jpg" alt="introduce_img" /></div>
			<div className="introduce">
				<p className="name"></p>
				{/* <p className="p_number">010-4572-2820</p>
				<p className="blog">https://blog.naver.com/jukistyle</p>
				<p className="git">https://github.com/zzang1537</p> */}
				{list.map((d,i)=>  <p key={i+1} className={d.props}>{d.value}</p> )}
				<p className="about">
					{/* <span>가나다라마바사아자차카타파하 블라블라블라 가나다라마바사아자차카타파하</span>
					<span>블라블라블라 가나다라마바사아자차카타파하 블라블라블라.</span>
					<span>라마바사아자차카타파하 블라블라블라.</span> */}
					{last.map((d, i)=><span key={i+1}>{d}</span> )}
				</p>
			</div>
		</Fragment>	
	);
}
export default IntroduceList;