import { useSelector } from 'react-redux';

function TopUtile(){
	let data=useSelector(pr => pr.utile);
	return(
		<div className="utile">
			<ul>
				{data.map((d,i) => 
					<li key={i+1}>
						<a href={d.link}>
							<span>{d.title}</span>
							<i><img src={"./images/"+d.alt+".png"} alt={d.alt}/></i>							
						</a>
					</li>
				)}
				{/* <li>
					<a href="https://github.com/zzang1537">
						<span>GitHub</span>
						<i><img src="/images/link1.png" alt="link1" /></i>							
					</a>
				</li>
				<li>
					<a href="https://codepen.io/your-work/">
						<span>CodePen</span>
						<i><img src="/images/link2.png" alt="link2" /></i>						
					</a>
				</li>
				<li>
					<a href="https://blog.naver.com/jukistyle">
						<span>Blog</span>
						<i><img src="/images/link3.png" alt="link3" /></i>						
					</a>
				</li> */}
			</ul>
			<a href="/" className="my_btn">
				<span>About_me</span>
				<i className="about_me"><img src="./images/about_me.png" alt="about_me" /></i>
			</a>
		</div>
	);
}
export default TopUtile;