import WorkList from "./WorkList";
import { useSelector } from 'react-redux';

function Page2(){
	let data=useSelector(pr => pr.tagList);
	let {list1, list2}=data;
	return(
		<section id="page2">
			<div className="inner">
				<div className="intro">
					<h3>PORTFOLIO</h3>
				</div>
				<div className="tab_box">
					<div className="tab_list">
						<ul>
							{list1.map((d,i)=> <li key={i+1}><a href="/">{d}</a></li> )}
							{/* <li><a href="/">검색엔진 최적화</a></li>
							<li><a href="/">그래픽 사용자 인터페이스</a></li> */}
						</ul>
					</div>
					<div className="tag_list">
						{list2.map((d,i) => <a key={i+1} href="/" className="tag">{d}</a> )}	
						{/* <a href="/" className="tag">#UI/UX기반 웹사이트</a>
						<a href="/" className="tag">#JavaScript Native</a>
						<a href="/" className="tag">#jQuery</a>
						<a href="/" className="tag">#SASS</a>
						<a href="/" className="tag">#SwiperJS</a>					
						<a href="/" className="tag">#HTML5 + CSS3</a> */}
					</div>
				</div>
				<WorkList />		
			</div>
		</section>
	);
}
export default Page2;