import { useSelector } from 'react-redux';
import MenuUtile from "./MenuUtile";

function Openmenu(){
	let data=useSelector(pr => pr.menu);
	return(
		<div className="total" id="open_menu">
			<div className="mymenu">
				<div className="menu_inner">
					<ul className="menuD">
						{data.map((d,i) => <li key={i+1}><a href="/">{d}</a></li>)}
					</ul>
				</div>
				<MenuUtile />					
			</div>
		</div>
	);
}
export default Openmenu;